/*_________________________ Grid _________________________ */
.grid__row {
  width: 100%;
  min-height: 1px;
  position: relative;
}

.grid__container {
  max-width: 1200px;
  width: 100%;
  padding: 0 25px;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
}

.grid__container--full-width {
  max-width: 100%;
  width: 100%;
  padding: 0 0;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
}

.grid__container--full-width-pad {
  max-width: 100%;
  width: 100%;
  padding: 0 25px;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
}

.grid__sidebar {
  width: 295px;
}

.grid__main {
  width: calc(100% - 295px);
  padding-bottom: 60px;
}

.grid__main-full-width {
  width: 100%;
}

.grid__col-1-1 {
  max-width: 100%;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}

.grid__col-1-1--sm {
  max-width: 523px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}

.grid__col-1-1--md {
  max-width: 750px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}

.grid__col-1-2 {
  width: 50%;
}

.grid__col-1-2:nth-of-type(odd) {
  padding-right: 12.5px;
}

.grid__col-1-2:nth-of-type(even) {
  padding-left: 12.5px;
}

@media only screen and (max-width: 768px) {
  .grid__col-1-2 {
